import React, { Component } from 'react'
// import { Tabs, Tab } from 'react-bootstrap'
import Tab from 'react-bootstrap/Tab';
import Tabs from 'react-bootstrap/Tabs';
import InputText from '../InputText-component/inputText'
import './Tabs.scss'
interface content {
    key: string
    title: string | JSX.Element
    component: React.ReactNode
}
interface Props {
  components: content[]
  small?:boolean
  active:string

}
const TabComponent: React.FC<Props> = ({components, active, small}:Props) => {
  return (
    <div>
      <Tabs defaultActiveKey={active} id='uncontrolled-tab-example' className='mb-3 tab-component'>
      {components.map((component,index)=>{
        return (
           <Tab className={index === components.length-1 ? 'nav-link last-child' : 'nav-link'} eventKey={component.key} title={component.title}>{component.component}</Tab>
        );
      })}
      </Tabs>
    </div>
  )
}

export default TabComponent;
