import React from 'react'
import Button from 'react-bootstrap/Button';
import {Form} from 'react-bootstrap';
import './EDTextInputs.scss';

interface Props {
  placeholder:string;
  controlId?:string;
  type:string;
  onChange:(e:any)=>void;
}
const EDTextInputs = ({placeholder, controlId, type, onChange}:Props) => {
  return (
    <Form className='ed-input'>
      <Form.Group className="mb-3" controlId={controlId}>
        <Form.Control type={type} placeholder={placeholder} className='input-field' onChange={onChange}/>
      </Form.Group>
    </Form>
  )
}

export default EDTextInputs