import React, { useEffect, useState } from 'react'
import './LoadStatistics.scss';
import { Container, Row, Col } from 'react-bootstrap';
import dropdownComponent from 'shared/components/dropdown-component';
import SearchInputField from 'shared/components/Search-input-field/search-input-field';
import Table from 'shared/components/Table-component/Table-component';
import Dropdown from 'shared/components/dropdown-component';
import TabComponent from 'shared/components/Tab-component/Tabs';
import TableIcon from 'assets/icons/menu-bar-tab.svg';
import Mapped from 'views/ValidationFlow/ValidationRecordDetails/Mapped';
import Unmapped from 'views/ValidationFlow/ValidationRecordDetails/Unmapped';
import CustomButton from 'shared/components/Button-component/Button';
import DownloadArrow from 'assets/icons/download.svg';
import ArrowIcon from 'assets/icons/CardArrowVector.svg';
import ValidationService from 'services/ValidationService/validation.service';
import MenuStack from 'assets/icons/menu-stack.svg';
import GraphView from 'assets/icons/graph-view-btn.svg';
import SortIcon from 'assets/icons/up-down-icon.svg';
import EDPagination from 'shared/components/Pagination';
import Loader from 'shared/components/Loader';
import VolatilityService from 'services/VolatilityService/volatility.service';
import { useNavigate } from 'react-router';
import { searchUtil } from 'shared/utils/searchUtil';
import ToastComponent from 'shared/components/Toast-component/Toast-component';
import EDDropdown from 'shared/components/EDDropdown';
import moment from 'moment';
import deepClone from 'shared/utils/deepClone';
interface Props {
  countryFilter?: string,
  dateFilter?: string,
}
const LoadStatistics = ({ countryFilter, dateFilter }: Props) => {
  const navigate = useNavigate();
  const [showA, setShowA] = useState(false);

  const [searchQuery, setSearchQuery] = useState<string>('');
  const { loading, VolatilityListData, fetchVolatilityList, setVolatilityListData, pagination, pageSize, toggleMappedCheckStatus, toggleAllCheckbox, toggleAllStatus, filteredData, resetAllCheckbox } = VolatilityService();
  const { countryList, providerMeta, fetchMetaData } = ValidationService();

  React.useEffect(() => {
    fetchVolatilityList();
    fetchMetaData();
  }, []);

  useEffect(() => {
    countryFilter && fetchVolatilityList({ filter_by_country: countryFilter });
  }, [countryFilter])

  const toggleShowA = () => setShowA(!showA);
  const handleCellNavigation = (cell: any) => {
    navigate(`mapping/${cell?.row?.original.id}`);
  }


  const handleFile = () => {
    navigate('/');
  }

  const handlePagination = (pageNumber: number) => {
    const limit = pageSize
    const offset = (pageNumber - 1) * limit
    fetchVolatilityList({
      limit,
      offset
    })
  }

  const columns: any = [
    {
      id: 'checkboxes',
      accessor: 'isSelected',
      Cell: ({ value, row }: any) => {
        return (
          <input
            type="checkbox"
            className="checkbox"
            checked={value}
            onChange={() => toggleMappedCheckStatus(row?.original?.id)}
          />
        );
      },
      Header: () => {
        return (
          <input
            type="checkbox"

            className="checkbox header-checkbox"
            checked={toggleAllStatus}
            onChange={() => toggleAllCheckbox()}
          />
        );
      },

    },
    {
      Header: 'Data Provider',
      accessor: 'SOURCE',
      minWidth: 150,
      Cell: (cell: any) => <div onClick={() => handleCellNavigation(cell)} onKeyDown={() => handleCellNavigation(cell)} role="link" tabIndex={0}>{cell.value}</div>

    },
    {
      Header: 'Country',
      accessor: 'country',
      minWidth: 120,
      Cell: (cell: any) => <div onClick={() => handleCellNavigation(cell)} onKeyDown={() => handleCellNavigation(cell)} role="link" tabIndex={0}>{cell.value}</div>
    },
    {
      Header: 'Category',
      accessor: 'category',
      minWidth: 100,
      Cell: (cell: any) => <div onClick={() => handleCellNavigation(cell)} onKeyDown={() => handleCellNavigation(cell)} role="link" tabIndex={0}>-</div>
    },
    {
      Header: 'Market',
      accessor: 'market',
      minWidth: 120,
      Cell: (cell: any) => <div onClick={() => handleCellNavigation(cell)} onKeyDown={() => handleCellNavigation(cell)} role="link" tabIndex={0}>{cell.value}</div>
    },
    {
      Header: 'Filename',
      accessor: 'FILENAME',
      Cell: (cell: any) => <div onClick={() => handleCellNavigation(cell)} onKeyDown={() => handleCellNavigation(cell)} role="link" tabIndex={0} className='cell-hyperlink'>{cell.value}</div>,
      minWidth: 100,

    },
    {
      Header: 'Loaded On',
      accessor: 'LOADSTARTTIME',
      Cell: ({ cell }: any) => <div onClick={() => handleCellNavigation(cell)} onKeyDown={() => handleCellNavigation(cell)} role="link" tabIndex={0}>{moment(cell.value).format("D/M/YYYY, hh:mm A")}</div>,
      width: 30
    },
    {
      Header: 'Remarks',
      accessor: 'remarks',
      minWidth: 100,
      Cell: (cell: any) => {
        return cell?.row?.original.PIPELINESTATUS === 'Failure'
          ? "Mandatory column missing"
          : <div onClick={handleFile} onKeyDown={handleFile} role="link" tabIndex={0}>-</div>
      },
    },
    {
      Header: 'Status',
      accessor: 'PIPELINESTATUS',
      minWidth: 100,
      Cell: (cell: any) => <div onClick={() => handleCellNavigation(cell)} onKeyDown={() => handleCellNavigation(cell)} role="link" tabIndex={0}>
        {cell.value === 'Success' ?
          (<div className='success-tag'>{cell.value}</div>) :
          cell.value === 'Failure' ? (<div className='failed-tag'>{cell.value}</div>) :
            (<div>{cell.value}</div>)}
      </div>
    },
    {
      Header: '',
      id: 'arrow',
      Cell: () => <img src={ArrowIcon} alt='arrow' />
    }
  ];

  const [selectedStatus, setSelectedStatus] = useState({
    "inProcess": false,
    "success": false,
    "failed": false
  })


  useEffect(() => {
    const { inProcess, success, failed } = selectedStatus

    let statusString = '';
    if (inProcess) statusString += "Running,"
    if (success) statusString += "Success,"
    if (failed) statusString += "Failure"

    fetchVolatilityList({
      filter_by_status: statusString
    })
  }, [selectedStatus])

  if (!VolatilityListData) return <Loader />

  const isSampleButtonEnable = VolatilityListData?.some((item) => item.isSelected === true)

  const selectedDataCount = VolatilityListData?.filter((item) => item.isSelected === true).length

  return (
    <Container fluid className='container-mapped'>
      <ToastComponent showState={showA} toggleShow={toggleShowA} message='Sample File Downloaded!' />
      <Row>
        <Col sm={6}>
          <div className='filters-container'>
            <div className='filter-container'>
              <EDDropdown label="Selected data provider" options={providerMeta} selectedOption="" handleOptionChange={(e) => {
                fetchVolatilityList({
                  filter_by_provider: e.target.value
                })
              }} />
            </div>
            <div className='filter-container'>
              <EDDropdown label="Selected category" options={[]} selectedOption="" handleOptionChange={(e) => {
                fetchVolatilityList({
                  filter_by_category: e.target.value
                })
              }}
              />
            </div>
            {/* <Dropdown className='dropdown-volatility' height='44px' options={dropdownOptions2} /> */}
          </div>
        </Col>
        <Col sm={6} className='search-container'>
          <SearchInputField
            onChange={searchUtil((e: any) => fetchVolatilityList({ search: e.target.value }))}
            placeholder='Search by file name, category...' width='354px' height='48px' />

          {/* Change View Buttons */}
        </Col>
      </Row>
      <Row>


        <div className='change-view-btns-container'>
          <div className='sort-by-button'>
            <h6>Sort By: <img src={SortIcon} alt='sortIcon' /></h6>
          </div>
          <p>View Information As: </p>
          <CustomButton primary >
            <img src={MenuStack} alt='table-icon' />
          </CustomButton>
          <CustomButton secondary >
            <img src={GraphView} alt='table-icon' />
          </CustomButton>
        </div>
      </Row>
      <Row className='table-top-container'>
        <Col sm={4} className='left-container'>
          <div className='btn-container'>
            {isSampleButtonEnable ? <CustomButton primary onClick={() => {
              toggleShowA()
              resetAllCheckbox()
            }}>
              <div className='btn-content'>
                Sample
                <img src={DownloadArrow} alt='down-arrow' />
              </div>
            </CustomButton>
              : <CustomButton disabled >
                <div className='btn-content'>
                  Sample
                  <img src={DownloadArrow} alt='down-arrow' />
                </div>
              </CustomButton>}
            {
              selectedDataCount !== 0
                ? <p>(Selected Record: {selectedDataCount})</p>
                : null
            }
          </div>
        </Col>


        <Col sm={8} className='right-container'>
          <p>Total Records: {VolatilityListData?.length}</p>
          <div className='separator'></div>
          <div className='success-btn-container'>
            <CustomButton height="34px"
              SecondaryPressed={!selectedStatus.inProcess}
              primary={selectedStatus.inProcess}
              onClick={() => {
                let _status = selectedStatus;
                _status.inProcess = !_status.inProcess
                setSelectedStatus(deepClone(_status))
              }}
            >InProcess</CustomButton>
            <CustomButton height="34px"
              SecondaryPressed={!selectedStatus.success}
              primary={selectedStatus.success}
              onClick={() => {
                let _status = selectedStatus;
                _status.success = !_status.success
                setSelectedStatus(deepClone(_status))
              }}
            >Successful</CustomButton>
            <CustomButton height="34px"
              SecondaryPressed={!selectedStatus.failed}
              primary={selectedStatus.failed}
              onClick={() => {
                let _status = selectedStatus;
                _status.failed = !_status.failed
                setSelectedStatus(deepClone(_status))
              }}
            >Failed</CustomButton>
          </div>
        </Col>

      </Row>
      <div className='pagination-container'>

        {/* <Table columns={columns} data={VolatilityListData} /> */}
        {
          loading
            ? <Loader />
            : <>
              <div className='table-container'>
                <Table columns={columns} data={VolatilityListData} />
              </div>
              {
                pagination && (
                  <EDPagination
                    page={pagination?.page ?? 0}
                    pageSize={pagination?.pageSize ?? 0}
                    totalCount={pagination?.totalCount ?? 0}
                    onPageItemClick={handlePagination}
                  />
                )
              }
            </>
        }

      </div >


      {/* <EDPagination
                    page={pagination?.page ?? 0}
                    pageSize={pagination?.pageSize ?? 0}
                    totalCount={pagination?.totalCount ?? 0}
                    onPageItemClick={handlePagination}
                  />
      
                 */}


    </Container>
  )
}

export default LoadStatistics