import React from 'react';
import { Button } from 'react-bootstrap';
import './Buttons.scss';
// import type Props from 'shared/types/Button.type';

interface Props {
  buttonNav?:boolean;
  buttonAction?:boolean;
  children: string | JSX.Element;
  active?: boolean;
  disabled?: boolean;
  primary?: boolean;
  secondary?:boolean;
  height?:string;
  onClick?:()=>void;
  SecondaryPressed?:boolean;
}


const CustomButton: React.FC<Props> = ({ children, buttonNav, buttonAction, active, disabled, primary, secondary, SecondaryPressed, height, onClick }: Props) => {
  let classNameNav = 'nav-font-buttons btn-styles';
  const classNameAction = 'action-button btn-styles';
  const styles= {
    height: height ?? '48px',
  }
  return (
    primary ? (<Button style={styles} variant="primary" className='btn-container primary' onClick={onClick}>{children}</Button>) : 
    disabled ? (<Button style={styles} className='btn-container disabled' onClick={onClick}>{children}</Button>) :
    buttonNav ? <Button style={styles}  className={classNameNav} onClick={onClick}>{children}</Button> :
    active ? <Button style={styles} className='nav-font-buttons btn-styles btn-active' onClick={onClick}>{children}</Button>:
    secondary ? <Button style={styles}  className='btn-container secondary' onClick={onClick}>{children}</Button>:
    SecondaryPressed ? <Button style={styles}  className='btn-container secondary pressed' onClick={onClick}>{children}</Button>:
    <Button style={styles} variant="primary" className='btn-container primary' onClick={onClick}>{children}</Button>

  )
  }

export default CustomButton;
