import React, { useEffect } from 'react';
import { Container, Row, Col, Breadcrumb } from 'react-bootstrap';
import DashboardContainer from 'shared/components/DashboardContainer-component/DashboardContainer';
import './VolatilityDashboard.scss';
import BreadcrumbComponent from 'shared/components/Breadcrumb-component/Breadcrumb-component';
import Dropdown from 'shared/components/dropdown-component';
import Mapped from 'views/ValidationFlow/ValidationRecordDetails/Mapped';
import Unmapped from 'views/ValidationFlow/ValidationRecordDetails/Unmapped';
import VolatilityService from 'services/VolatilityService/volatility.service';
import TabComponent from 'shared/components/Tab-component/Tabs';
import LoadStatistics from './LoadStatistics';
import Status from './Status';
import EDDropdown from 'shared/components/EDDropdown';
import Traffic from './Traffic';
import { AppRoutes } from 'routes/routeConstants/appRoutes';
import { useNavigate } from 'react-router';
import ValidationService from 'services/ValidationService/validation.service';
import EDDatePicker from 'shared/components/EDDatePicker';
const VolatilityDashboard = () => {
  const navigate = useNavigate();
  const countryListdropdownOptions = [
    {
      value: 'this week',
      label: 'Duration:This Week'
    }
  ]
  const styles = {
    padding: '0px 12px 0px 0px'
  }
  const { countryList, providerMeta, fetchMetaData } = ValidationService();
  useEffect(() => {
    fetchMetaData();
  }, [])
  const [selectedOption, setSelectedOption] = React.useState("");
  const [selectedTime, setSelectedTime] = React.useState("");

  const handleOptionChange = (event: any) => {
    setSelectedOption(event.target.value);
  };

  return (
    <DashboardContainer>
      <Container fluid className='page-container'>
        <Row className='top-row'>
          <Col>
            <p className='page-heading'>File Volatility</p>
            <BreadcrumbComponent
              links={[{ link: 'Reports', active: false, onClick: () => navigate(`/${AppRoutes.FILE_VOLATILITY_HOME}`) },
              { link: 'File Volatility', active: true }]}
            />
          </Col>
          <Col className='filters-container'>
            <p className='m-0 mr-3'>Filter: </p>
            <div className='filter-container'>
              <EDDatePicker />
            </div>
            <div className='filter-container'>
              <EDDropdown label="Country" options={[
                {
                  label: "India",
                  value: "India"
                },
                {
                  label: "USA",
                  value: "USA"
                }
              ]} selectedOption={selectedOption} handleOptionChange={(e) => {
                setSelectedOption(e.target.value)
              }} />
            </div>
          </Col>
        </Row>
        <Row className='databar-container'>
        </Row>
        <Row className='tab-container'>
          <TabComponent active='loadStatistics'
            components={[
              { key: 'traffic', title: 'Traffic', component: <Traffic /> },
              { key: 'status', title: 'Status', component: <Status /> },
              { key: 'loadStatistics', title: 'Load Statistics', component: <LoadStatistics dateFilter={selectedTime} countryFilter={selectedOption} /> }
            ]} />
        </Row>
      </Container>
    </DashboardContainer>
  )
}

export default VolatilityDashboard;