import React, { useEffect } from 'react'
import DashboardContainer from 'shared/components/DashboardContainer-component/DashboardContainer'
import BreadcrumbComponent from 'shared/components/Breadcrumb-component/Breadcrumb-component'
import Dropdown from 'shared/components/dropdown-component'
import SearchInputField from 'shared/components/Search-input-field/search-input-field'
import Databar from 'shared/components/Databar-component/Databar'
import TabComponent from 'shared/components/Tab-component/Tabs'
import Mapped from './Mapped'
import Unmapped from './Unmapped'
import { Container, Row, Col } from 'react-bootstrap'
import './ValidationRecordDetails.scss'
import ValidationService from 'services/ValidationService/validation.service'
import { useLocation, useNavigate, useParams } from 'react-router'
import moment from 'moment'
import AppRouter from 'routes'
import { AppRoutes } from 'routes/routeConstants/appRoutes'

const ValidationRecordDetails = () => {

  const { fetchIndividualSmartMapping, smartMapping } = ValidationService()
  const { id } = useParams();
  const navigate = useNavigate()
  const {state} = useLocation();

  useEffect(() => {
    id && fetchIndividualSmartMapping(id)
  }, [])

  if (!id) return null
  if (!smartMapping) return null

  const mapped: number = parseInt(smartMapping.mappedRecordCount ?? "0") ?? 0
  const unmapped: number = parseInt(smartMapping.unmappedRecordCount ?? "0") ?? 0

  const totalRecord: number = mapped + unmapped
  const formattedDate = moment(smartMapping?.createdAt).format("D/M/YYYY, hh:mm A")

  return (
    <DashboardContainer >
      <Container fluid className='page-container'>
        <Row className='breadcrumb-container'>
          <p className='page-heading'>Record Detail</p>
          <BreadcrumbComponent links={[
            { link: 'Checks', active: false, onClick: () => navigate(`/${AppRoutes.VALIDATION_HOME}`, { replace: true }) },
            { link: 'Validations', active: false, onClick: () => navigate(`/${AppRoutes.VALIDATION_HOME}/${AppRoutes.VALIDATION_RECORDS}`, { replace: true }), },
            { link: 'Record Detail', active: true }]} />
        </Row>
        <Row className='databar-container'>
          <Databar DataProvider={smartMapping?.dataProvider ?? ""}
            TotalRecords={totalRecord}
            successStatus={smartMapping?.successStatus ?? 0}
            loadedOn={formattedDate} />
        </Row>
        <Row className='tabs-container'>
          <TabComponent active={state} components={[{ key: 'mapped', title: 'Mapped', component: <Mapped /> }, { key: 'unmapped', title: 'Unmapped', component: <Unmapped /> }]} />
        </Row>
      </Container>
    </DashboardContainer >
  )
}

export default ValidationRecordDetails