import React, { useState } from "react";
import './EDDropdown.scss';
interface option {
  id?:number
  name?:string
  label?:string
  value?:string
  createdAt?:string
  updatedAt?:string
}
interface Props {
  options:option[]
  label:string
  selectedOption:string
  handleOptionChange:(event:any)=>void
}
const EDDropdown = ({ options, selectedOption, handleOptionChange, label }:Props) => {
  
  return (
    <div className="dropdown-container">
      <div className="label-container"><p className='label-text m-0 p-0'>{label}</p></div>
      <select  onChange={handleOptionChange} className='filtering-container'>
        <option value="">All</option>
        {options.map((option) => (
          <option key={option.value} value={option.value}>
            {option.label}
          </option>
        ))}
      </select>
    </div>
  );
}

export default EDDropdown
