import React, { useEffect, useState } from 'react'
import DashboardContainer from 'shared/components/DashboardContainer-component/DashboardContainer'
import { Container, Row, Col } from 'react-bootstrap';
import BreadcrumbComponent from 'shared/components/Breadcrumb-component/Breadcrumb-component';
import Error from 'assets/icons/error.svg';
import SearchDropdown from 'shared/components/SearchDropdown';
import Download from 'assets/icons/download-icon.svg';
import LeftArrow from 'assets/icons/left-arrow.svg';
import UpdateIcon from 'assets/icons/update-icon.svg'
import Upload from 'assets/icons/upload.svg';
import RedirectLeft from 'assets/icons/redirect-left.svg'
import RedirectRight from 'assets/icons/redirect-right.svg'
import './VolatilityMapping.scss';
import CustomButton from 'shared/components/Button-component/Button';
import UpdateTargetModal from 'shared/components/update-target-modal/UpdateTargetModal';
import RemarksModal from 'shared/components/remarks-modal/RemarksModal';
import VolatilityService from 'services/VolatilityService/volatility.service';
import { useNavigate, useParams } from 'react-router';
import moment from 'moment';
import { Mappings } from 'models/Mappings/mappings.model';
import { AppRoutes } from 'routes/routeConstants/appRoutes';
import ToastComponent from 'shared/components/Toast-component/Toast-component';
import Loader from 'shared/components/Loader';

const VolatilityMapping = () => {
  const { fetchIndividualFileVolatility, individualFileVolatilityData, fetchColumnMappings, columnMappings, updateMappingInState, updateMapping, buttonLoading } = VolatilityService();

  const [modalShow, setModalShow] = React.useState(false);
  const [UpdateModalShow, setUpdateModalShow] = React.useState(false);
  const [showToast, setShowToast] = useState<boolean>(false)
  const [enableSubmitButton, setEnableSubmitButton] = useState(false)
  const navigate = useNavigate();

  function handleModalShow() {
    setModalShow(true);
  }
  function handleUpdateModalShow() {
    setUpdateModalShow(true);
  }
  // Getting id from the URL
  const { id } = useParams();

  // Fetching the single Volatility Data
  useEffect(() => {
    id && fetchIndividualFileVolatility(id);
    id && fetchColumnMappings(id);
  }, []);

  // If id not present in the URL, return null
  if (!id) return null;
  if (!columnMappings) return <Loader />;

  const menuItems1 = [
    {
      key: 'Data Provider',
      value: individualFileVolatilityData?.SOURCE
    },
    {
      key: 'Country',
      value: individualFileVolatilityData?.country
    },
    {
      key: 'Market',
      value: individualFileVolatilityData?.market
    },
  ]

  const menuItems2 = [
    {
      key: 'Status',
      value: individualFileVolatilityData?.PIPELINESTATUS
    },
    {
      key: 'Remarks',
      value: "-"
    },
    {
      key: 'Loaded On',
      value: moment(individualFileVolatilityData?.LOADSTARTTIME).format("D/M/YYYY, hh:mm A")

    }
  ]

  const totalCOlumns = columnMappings?.mappings?.length

  const errorColumns: Mappings[] | undefined = columnMappings?.mappings?.filter((item) => item.source === '')
  const errorColumnLength = errorColumns ? errorColumns?.length : 0

  if (!columnMappings.sourceColumns) return null

  const optionItems = columnMappings?.sourceColumns?.map((item, index) => {
    return {
      key: index.toString(),
      value: item
    }
  })

  const sendDataToServer = async () => {
    await updateMapping()
    fetchIndividualFileVolatility(id);
    fetchColumnMappings(id);
    toggleToast()
  }


  const toggleToast = () => {
    setShowToast(!showToast)
  }

  return (
    <DashboardContainer>
      <ToastComponent showState={showToast} toggleShow={toggleToast} message='Mappings updated' />

      <Container fluid className='volatility-mapping'>
        <RemarksModal
          show={modalShow}
          onHide={() => setModalShow(false)}
          className='remarks-modal'
        />
        <UpdateTargetModal
          show={UpdateModalShow}
          onHide={() => setUpdateModalShow(false)}
          className='remarks-modal'
        />
        {/* Breadcrumb */}
        <Row className='top-row'>
          <Col>
            <p className='page-heading'>Mapping</p>
            <BreadcrumbComponent
              links={[
                { link: 'Load Statistics', active: false, onClick: () => navigate('/reports/file-volatility') },
                { link: 'Mapping', active: true }]}
              section />
          </Col>
        </Row>
        {/* Filename heading */}
        <Row>
          <div className='file-name-row'>
            <div className='file-change-btn'><img src={RedirectLeft} alt='left-arrow' /></div>
            <p>Filename:</p>
            <span className='hyper-link'>File Name: {individualFileVolatilityData?.FILENAME}</span>
            <div className='file-change-btn'><img src={RedirectRight} alt='right-arrow' /></div>
          </div>
        </Row>
        {/* Menu */}
        <Row className='menu-row'>
          <Col sm={4} className='left-container'>
            {menuItems1.map((item, index) => {
              return (
                <div key={index} className='item'>
                  <h2>{item.key}: </h2>
                  <p> {item.value}</p>
                </div>
              )
            })}
          </Col>
          <Col sm={4} className='left-container'>
            {menuItems2.map((item, index) => {
              return (
                <div key={index} className='item'>
                  <h2>{item.key}: </h2>
                  {item.value === 'Failed' ? <p className='failed-tag small-text'>{item.value}</p> : <p >{item.value}</p>}
                </div>
              );
            })}
          </Col>
          {/* Button: Excel and Remarks */}
          <Col sm={4} className='right-container'>
            <div className='excel-button'>
              <p>Excel:</p>
              <div className='arrow-button'>
                <img src={Download} alt='download' />
              </div>
              <div className='arrow-button'>
                <img src={Upload} alt='upload' />
              </div>
            </div>
            <div className='remarks-button' role='button' onClick={() => handleModalShow()} onKeyDown={() => handleModalShow()} tabIndex={0}>
              <p><img src={LeftArrow} alt='left-arrow' /></p>
              <div>Remarks</div>
            </div>
          </Col>
        </Row>
        {/* Table Container */}

        {
          individualFileVolatilityData?.PIPELINESTATUS !== 'failed'
            ? <Row className='table-container'>
              <Col sm={6} className='left-table-container'>
                <Row className='header'>
                  <div>
                    <Col className='source-header' sm={3}><div>Source({individualFileVolatilityData?.SOURCE})</div></Col>
                    <Col className='second' sm={6}>
                      <div className='col-number'>{totalCOlumns} columns</div>
                      <div className='error'>
                        <img src={Error} alt='error' />
                        {errorColumnLength} Errors
                      </div>
                    </Col>
                  </div>
                </Row>
              </Col>
              <Col sm={6} className='right-table-container'>
                <div><p className='target-header'>Target (Unilever database)</p>
                  <div className='update-button' role='button' onClick={() => handleUpdateModalShow()} onKeyDown={() => handleUpdateModalShow()} tabIndex={0}>
                    <img src={UpdateIcon} alt='update' />
                    <div className='update-action'>Update</div>
                  </div>
                </div>
                <div>
                </div>
              </Col>
              <Col className='end-table-container' sm={3}>

              </Col>
              <Row className='table-content'>
                <Col sm={12} className='left-col'>
                  {
                    columnMappings?.mappings?.map((item, index) =>
                      <Row className='row-item' key={index}>
                        <Col sm={6}>
                          <div className='item'>
                            <SearchDropdown
                              optionItems={optionItems ?? []}
                              className={item.source!=='' ? 'data': 'error-tag'}
                              value={item.source ?? "-"}
                              currentItemIndex={index}
                              updateTargetValue={(name, index) => {
                                updateMappingInState(name, index)
                                setEnableSubmitButton(true)
                              }}
                            />
                          </div>
                        </Col>
                        <Col sm={6} className='right-col d-flex'>
                          <div className='item'>
                            <p>{item.target}</p>
                          </div>
                        </Col>
                      </Row>
                    )
                  }
                </Col>

              </Row>

              <div className='last-row-btns'>
                <CustomButton secondary onClick={() => {
                  navigate('/reports/file-volatility');
                }}>Cancel</CustomButton>
                <CustomButton
                  disabled={!enableSubmitButton}
                  onClick={() => {
                    sendDataToServer()
                  }}
                >
                  {buttonLoading ? 'Submitting...' : 'Submit'}
                </CustomButton>
              </div>

            </Row>
            : null
        }

      </Container>
    </DashboardContainer>
  )
}

export default VolatilityMapping