
import {serializable, alias, object, list, primitive} from 'serializr';

export class IndividualFileVolatility { 

	@serializable(alias('id', primitive()))
	id?: number;

	@serializable(alias('PIPELINE_RUN_ID', primitive()))
	PIPELINERUNID?: string;

	@serializable(alias('LOAD_DESC', primitive()))
	LOADDESC?: string;

	@serializable(alias('LOAD_START_TIME', primitive()))
	LOADSTARTTIME?: string;

	@serializable(alias('LOAD_END_TIME', primitive()))
	LOADENDTIME?: string;

	@serializable(alias('SOURCE', primitive()))
	SOURCE?: string;

	@serializable(alias('FILE_NAME', primitive()))
	FILENAME?: string;

	@serializable(alias('FILE_LAST_MODIFIED_DATE', primitive()))
	FILELASTMODIFIEDDATE?: string;

	@serializable(alias('PIPELINE_STATUS', primitive()))
	PIPELINESTATUS?: string;

	@serializable(alias('createdAt', primitive()))
	createdAt?: string;

	@serializable(alias('updatedAt', primitive()))
	updatedAt?: string;

	@serializable(alias('MARKET', primitive()))
	market?: string;

	@serializable(alias('COUNTRY', primitive()))
	country?: string;

	@serializable(primitive())
	isSelected?: boolean = false;

	@serializable(primitive())
	isChecked?: boolean = false;


}