
import {Mappings} from '../Mappings/mappings.model';

import {serializable, alias, object, list, primitive} from 'serializr';

export class ColumnMappings { 

	@serializable(alias('source_column', list(primitive())))
	sourceColumns?: string[] = [];

	@serializable(alias('mappings', list(object(Mappings))))
	mappings?: Mappings[] = [];

}