import React from 'react';
import {Modal, Button, Form} from 'react-bootstrap';
import Send from 'assets/icons/send-icon.svg';
import './UpdateTargetModal.scss';
import CustomButton from '../Button-component/Button';
import { Container, Row, Col} from 'react-bootstrap';
import DisabledGreenCheck from 'assets/icons/disabled-green-check.svg';
import EDTextInputs from '../EDTextInputs';
import DeleteCross from 'assets/icons/delete-cross.svg';
import { setIn } from 'formik';

const UpdateTargetModal = (props:any) => {
  const [inputValue, setInputValue] = React.useState('');
  
  const isTrue = () => {
    return inputValue.length > 6;
  };
  console.log(inputValue);
  const existing = [
    'TAG',
    'SHORT',
    'DISPLAY_ORDER',
    'PARENT_TAG',
    'HIER_NUM',
    'HIER_LEVEL_NAME',
    'HIER_LEVEL_NAME1'
  ]
  const [existingArr, setExistingArr] = React.useState<string[]>(existing);
  const handleSubmit = (array:string[], inputValue:string) =>{
      array.unshift(inputValue)
      setExistingArr(array);
  }
  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
      className='update-target-modal'
    >
      <Modal.Header closeButton className='header-container'> 
        <Modal.Title id="contained-modal-title-vcenter">
          <div className='modal-heading'>
            <p>Update Target</p>
            </div>
      </Modal.Title>
      </Modal.Header>
      <Modal.Body className="show-grid">
        <Container fluid >
          <div className='body-container'>
          <Row>
            <Col xs={6} md={5} className='left-container'>
              <div className='left-content'>
                <h2>Add New</h2>
                <p>To add a new item type in the box and click on tick mark.</p>
              </div>
              <div className='textinput-container'>
              <Col sm={10}>
              <EDTextInputs placeholder='Type Here'  type='text' onChange={(e:any)=>setInputValue(e.target.value)}/>
              </Col>  
                {isTrue() ? (
                <div role='button' onClick={()=>{handleSubmit(existing, inputValue)}} onKeyDown={()=>{handleSubmit(existing, inputValue)}} tabIndex={0}><img src={DisabledGreenCheck} alt='green check' className='enabled'/></div>) :(<div role='button'><img src={DisabledGreenCheck} alt='disabled green check' className='disabled'/></div>)}
              </div>
            </Col>
            <Col xs={6} md={5} className='right-container'>
            <div className='right-content'>
              <div>
                <h2>Existing ({existing.length})</h2>
                <p>To remove existing data, click on cross next to the data itself.</p>
                </div>
                <div className='list-item-container'>
                  {existingArr.map((item, index)=>{
                    return (
                      <div className='list-item-parent'>
                      <div key={index} className='list-item'>
                    <p>{item}</p>
                    </div>
                    <img src={DeleteCross} alt='delete' />
                    </div>
                    );
                    
                  })}
                  </div>
              </div>
            </Col>
          </Row>
          </div>
        </Container>
      </Modal.Body>
      <Modal.Footer className='modal-footer'>
      <CustomButton secondary onClick={props.onHide}>Cancel</CustomButton>
      <CustomButton primary onClick={props.onHide}>Submit</CustomButton>
      </Modal.Footer>
    </Modal>
  )
}

export default UpdateTargetModal