import React, { useEffect } from 'react'
import { Container, Row, Col } from 'react-bootstrap'
import DashboardContainer from 'shared/components/DashboardContainer-component/DashboardContainer'
import Cards from 'shared/components/Card-component/Cards'
import { useNavigate } from 'react-router';
import { AppRoutes } from 'routes/routeConstants/appRoutes';
import FileVolatility1 from 'assets/icons/file-volatility-card1.svg'
import FileVolatility2 from 'assets/icons/file-volatility-card2.svg'
import FileVolatility3 from 'assets/icons/file-volatility-card3.svg'
import FileVolatility4 from 'assets/icons/file-volatility-card4.svg'
import VolatilityService from 'services/VolatilityService/volatility.service';
import './VolatilityHome.scss';
import Loader from 'shared/components/Loader';
const VolatilityHome = () => {
  const {fetchVolatilityDashboard, loading, volatilityDashboardDetails} = VolatilityService();
  const navigate = useNavigate();
  const redirectToValidationPage = () => {
    navigate(AppRoutes.FILE_VOLATALITY_RECORDS)
  }
  useEffect(()=>{
    fetchVolatilityDashboard();
  },[]);
  console.log("data: ",volatilityDashboardDetails);
  return (
    <DashboardContainer>
    <div className='reports-page-content'>
      <Row>
        <Col xs={12}>

          <h1 className='page-title'>Reports</h1>
          <p className='page-sub-title'>
            Lorem ipsum dolor sit amet, consectetur adipiscing elit ut aliquam, purus sit amet luctus venenatis, lectus magna fringilla urna, porttitor rhoncus dolor purus non enim praesent elementum facilisis leo, vel fringilla est ullamcorper eget nulla facilisi etiam dignissim diam.
          </p>
        </Col>
      </Row>
      {loading ? <Loader/> 
      :
      <Row>
            <Col xs={4}>
              <Cards
                heading='File Volatility'
                value={volatilityDashboardDetails?.totalCount ?? 5}
                footer1Heading='Excel'
                footer1Value={volatilityDashboardDetails?.excelCount ?? 0}
                footer2Heading='Csv'
                footer2Value={volatilityDashboardDetails?.csvCount ?? 5}
                footer3Heading='Word'
                footer3Value={volatilityDashboardDetails?.docCount ?? 0}
                image={FileVolatility1}
                cardHeaderClickHandler={redirectToValidationPage}
              />
            </Col>
            <Col xs={4}>
              <Cards 
                heading='Data Quality'
                value={62}
                footer1Heading='Recent'
                footer1Value={10}
                footer2Heading='Old'
                footer2Value={60}
                image={FileVolatility2}
              />
            </Col>
            <Col xs={4}>
            <Cards 
                heading='Harmonised Data'
                value={1162}
                footer1Heading='Files'
                footer1Value={10}
                footer2Heading='Videos'
                footer2Value={60}
                image={FileVolatility3}
              />
            </Col>
            <Col xs={4}>
            <Cards 
                heading='Logs'
                value={119}
                footer1Heading='Excel'
                footer1Value={10}
                footer2Heading='Csv'
                footer2Value={60}
                footer3Heading='Word'
                footer3Value={2}
                image={FileVolatility4}
              />
            </Col>
          </Row>
}
      
    </div>
      
  </DashboardContainer>
  )
}

export default VolatilityHome