import React from 'react';
import { Col, Row, Toast, ToastContainer } from 'react-bootstrap';
import "./Toast-component.scss"
interface Props {
    showState: boolean
    toggleShow: () => void
    message: string
}

const ToastComponent: React.FC<Props> = ({ showState, toggleShow, message }: Props) => {
    return (
        <div>
            <ToastContainer position="top-end" className='toast-component'>
                <Toast className='toast-component-toast' show={showState} onClose={toggleShow}>
                    <Toast.Header className='toast-component-header' closeVariant="white">
                        <Row>
                            <Col className='toast-status' xl={4}>
                                <strong>Successful: </strong>
                            </Col>
                            <Col className="toast-content">
                                {message}
                            </Col>
                        </Row>
                    </Toast.Header>
                </Toast>
            </ToastContainer>

        </div >
    );
}

export default ToastComponent;
